.bibleBook {
  display: flex;
  justify-content: center;
  gap: 2em;
}
#sidebar {
  width: 15rem;
  background-color: #f7f7f7;
  border-right: solid 1px #e3e3e3;
  display: flex;
  flex-direction: column;
  margin-left: 10px;
  border: 1px solid #e0dede;
  border-radius: 20px;
  height: 600px;
}

#sidebar > * {
  padding-left: 2rem;
}

#sidebar h1 {
  font-size: 1rem;
  font-weight: 500;
  display: flex;
  align-items: center;
  margin: 0;
  padding: 1rem 2rem;
  border-top: 1px solid #e3e3e3;
  order: 1;
  line-height: 1;
}

#sidebar > div {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid #e3e3e3;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#sidebar nav {
  flex: 1;
  overflow: auto;
  padding-top: 1rem;
}

#sidebar nav a span {
  float: right;
  color: #eeb004;
}
#sidebar nav a.active span {
  color: inherit;
}

i {
  color: #818181;
}
#sidebar nav .active i {
  color: inherit;
}

#sidebar ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

#sidebar li {
  margin: 0.25rem 0;
}

#sidebar nav a {
  display: flex;
  align-items: center;
  justify-content: space-between;
  overflow: hidden;

  white-space: pre;
  padding: 0.5rem;
  border-radius: 8px;
  color: inherit;
  text-decoration: none;
  //   gap: 1rem;
}

#sidebar nav a:hover {
  background: #e3e3e3;
}

#sidebar nav a.active {
  background: hsl(224, 98%, 58%);
  color: white;
}

#sidebar nav a.pending {
  color: hsl(224, 98%, 58%);
}

.content {
  background-image: url(../../assets/bg.webp);
  width: 78%;
  border-radius: 20px;
  padding: 20px;
  font-size: 1rem;
}

.verseNo {
  user-select: none;
}
.verse-actions {
    display: flex;
    flex-direction: column;
    z-index: 99;
    margin-top: .5em;
    background-color: white;
    padding: 16px;
    width: fit-content;
    box-shadow: 5px 5px 5px #f5f5f5;

    .actions {
        display: flex;
    }

    .highlighter {
        display: flex;
        gap: 10px;
        margin-top: 5px;
    }

    .highlight {
        border: none;
        border-radius: 50%;
        height: 30px;
        width: 30px;
    }

}
.copier {
  color: white;
  padding: 5px 15px;
  margin-left: 5px;
  margin-top: 5px;
  border-radius: 10px;
  border: none;
  background-color: hotpink;

}

.chapter article {
  width: 100%;
  column-width: 400px;
  column-gap: 2em;
}

@media screen and (max-width: 767px) {
  #sidebar {
    display: none;
  }
}
