@import url('https://fonts.googleapis.com/css2?family=Alfa+Slab+One&family=Bebas+Neue&family=Gentium+Book+Basic&family=Open+Sans:wght@700&display=swap');

.body {
  display: grid;
  margin: auto;
  grid-template-columns: 100%;
  grid-template-rows: auto 1fr auto;
  gap: 10px;
  width: 80%;
  min-height: 100vh;
  background-color: whitesmoke;
  color: #1e1f1e;
  //   background-image: url(./assets/bg.webp);
  font-family: 'Gentium Book Basic', serif;
  box-shadow: 5px 5px 30px 5px #4a4848;
}

header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.details {
    height: 80%;
    width: 100%;
}
h1 {
  text-align: center;
  text-transform: uppercase;
  font-family: 'Open Sans', sans-serif;
  font-size: 4rem;
  color: transparent;
  background-image: url(../assets/plants-bg.jpg);
  background-repeat: repeat;
  background-size: contain;
  background-clip: text;
  margin-top: 2em;
  letter-spacing: 3px;
}

.alert {
    opacity: 0.55;
    color: white;
    font-size: 1.1rem;
    padding: 20px;
    border-radius: 10px;
}
.footer-section {
    display: flex;
    justify-content: center;
    width: 80%;
    margin: 0 auto;
    font-family: 'Gentium Book Basic', serif;
}
footer {
    height: 2em;
    display: grid;
    place-content: center;
    font-size: 1.2rem;
    background-color: rgba($color: #f5f5f5, $alpha: 1);
    color: #1e1f1e;
    padding: 30px 16px;
    width: 100%;
    margin-top: 2em;
}
 a {
    text-decoration: none;
 }

 @media screen and (max-width: 280px) {
    .body {
        width: 100%;
    }
 }